.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  // align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;
  }

  .header-blocks {
    width: 23%;

    &:last-child {
      width: 8%;
    }
  }

  .total {
    font-size: 30px;
    font-weight: bold;
    display: flex;
    flex-direction: row-reverse;
  }
}
